<template>
    <div class="body">
        <div class="title has-text-centered">
        Mint {{symbol}}
        </div>
        <div class="level item">
            <div class="level-left">
                <div class="level-item">
                <strong>Price Feed</strong>
                </div>
            </div>
            <div class="level-right">
                <div class="level-item is-pulled-right">
                {{priceFeed}}
                </div>
            </div>
        </div>

        <div class="level item">
            <div class="level-left">
                <div class="level-item">
                <strong>Current Index</strong>
                </div>
            </div>
            <div class="level-right">
                <div class="level-item is-pulled-right">
                {{price}}
                </div>
            </div>
        </div>

        <div class="level item">
            <div class="level-left">
                <div class="level-item">
                <strong>Expiration</strong>
                </div>
            </div>
            <div class="level-right">
                <div class="level-item is-pulled-right">
                {{timeFormat(expirationTimestamp)}}
                </div>
            </div>
        </div>

        <div class="level item">
            <div class="level-left">
                <div class="level-item">
                <strong>Collateral</strong>
                </div>
            </div>
            <div class="level-right">
                <div class="level-item is-pulled-right">
                {{collateralSymbol}}
                </div>
            </div>
        </div>

        <div class="level item">
            <div class="level-left">
                <div class="level-item">
                <strong>Minimum Collateral</strong>
                </div>
            </div>
            <div class="level-right">
                <div class="level-item is-pulled-right">
                {{creq}}%
                </div>
            </div>
        </div>

        <div class="level item">
            <div class="level-left">
                <div class="level-item">
                <strong>Liquidation Threshold</strong>
                </div>
            </div>
            <div class="level-right">
                <div class="level-item is-pulled-right">
                {{liquidationThresh}}%
                </div>
            </div>
        </div>

        <div class="level item">
            <div class="level-left">
                <div class="level-item">
                <strong>Minimum Minted Tokens</strong>
                </div>
            </div>
            <div class="level-right">
                <div class="level-item is-pulled-right">
                {{balanceFormat(minSponsorTokens)}}
                </div>
            </div>
        </div>

        <hr class="divider">

        <section>
            <b-field label="Deposit Collateral">
                <b-field>
                    <b-input v-model="collateralAmount" :placeholder="'Deposit ' + collateralSymbol" expanded></b-input>
                    <p class="control">
                        <b-button @click="maxCollateral" class="button">MAX</b-button>
                    </p>
                </b-field>
            </b-field>
            <div class="balance">
                <div class="is-pulled-left">
                    Balance: {{balanceFormat(collateralBalance)}} {{collateralSymbol}}
                </div>
            </div>
            <b-field label="Mint Synth">
                <b-field>
                    <b-input v-model="synthAmount" :placeholder="'Mint ' + symbol" expanded></b-input>
                    <p class="control">
                        <b-button @click="maxSynth" class="button">MAX</b-button>
                    </p>
                </b-field>
            </b-field>
            <div class="balance">
                <div class="is-pulled-left">
                    Balance: {{balanceFormat(synthBalance)}} {{symbol}}
                </div>
            </div>
            <div id="cratio">Collateralization Ratio</div>
            <b-progress :value="cratio" :type="progressColor" :max="Number(creq)" show-value></b-progress>
            <b-message v-if="cratio < creq && cratio > 0" title="Insufficient Collateral" type="is-danger" :closable="false">
            Your collateral ratio is below the minimum collateral ({{creq}}%)
            </b-message>
            <b-message v-if="synthAmount.length > 0 && synthAmount < parseInt(balanceFormat(minSponsorTokens))" title="Below Minimum Minting Amount" type="is-danger" :closable="false">
            The synth amount you have entered is below the minimum ({{balanceFormat(minSponsorTokens)}})
            </b-message>
            <b-button :disabled="disabled" @click="mint" id="mint-button" type="is-primary">Mint {{symbol}}</b-button>
        </section>
    </div>
</template>

<script>
import { ethers } from 'ethers';
import BigNumber from 'bignumber.js'

export default {
  name: 'Mint',
  props:['name', 'symbol', 'priceFeed', 'price', 'expirationTimestamp', 'creq', 'collateralBalance', 'collateralSymbol', 'synthBalance', 'liquidationThresh', 'minSponsorTokens'],
  data:()=>{
      return {
          collateralAmount:"",
          synthAmount:""
      }
  },
  methods:{
      timeFormat (timestamp) {
            return (new Date(timestamp*1000)).toUTCString()
      },
      balanceFormat(baseUnit) {
            return ethers.utils.formatEther(baseUnit)
      },
      maxCollateral () {
            this.collateralAmount = this.balanceFormat(this.collateralBalance)
      },
      maxSynth () {
            if(isNaN(this.collateralAmount)) return
            this.synthAmount = (this.collateralAmount / this.price) / (this.creq * 0.01)
      },
      mint() {
        this.$emit('mint', {
            collateral:this.collateralAmount,
            synth:this.synthAmount,
            synthName:this.name
        })
        this.$emit('close')
      }
  },
  computed: {
      progressColor () {
        if(this.cratio >= this.creq) {
            return 'is-success'
        } else {
            return "is-danger"
        }
      },
      cratio () {
          if(this.collateralAmount === "" || this.synthAmount === "") return 0
          if(isNaN(this.collateralAmount) || isNaN(this.synthAmount)) return 0
          const BigCollateralAmount = BigNumber(this.collateralAmount).shiftedBy(18)
          const BigSynthAmount = BigNumber(this.synthAmount).shiftedBy(18)
          return BigCollateralAmount.div(
              BigSynthAmount.times(this.price)
          ).times(100).toNumber()
      },
      disabled () {
          if(this.cratio < this.creq) return true
          if(isNaN(this.collateralAmount) || isNaN(this.synthAmount)) return true
          if(this.synthAmount < parseInt(this.balanceFormat(this.minSponsorTokens))) return true
          if(this.collateralAmount > parseInt(this.balanceFormat(this.collateralBalance))) return true
          if(this.synthAmount.length === 0 || this.collateralAmount.length === 0) return true
          if(this.synthAmount === 0) return true
          return false
      }
  }
}
</script>

<style scoped>
.body {
    max-width:400px;
    margin:auto;
    padding-top:50px;
    padding-bottom:50px;
}
.item {
    margin-bottom:20px;
}
.divider {
    border-top: 1px solid #000;
    margin:40px 80px;
}
#mint-button {
    display:block;
    margin:auto;
}
.balance {
    display:block;
    height:30px;
    margin-bottom:30px;
}
#cratio {
    margin-bottom:10px;
    font-weight:bold;
}
</style>
